import React from 'react'
import { Link } from 'react-router-dom';
import { useStoreState } from '../Redux/hooks';
import Idata from './interfaces';

interface Props {
    data: Idata;
    deleteItem(uniqueid: number): void;
    editItems(uniqueid: number): void;
    userIndex(uniqueid: number): void;
}

const Displayuser = ({ data, deleteItem, editItems, userIndex }: Props) => {
    const userdata = useStoreState((store) => store.users.items);

    // const userIndex = (useridx: number) => {
    //     const items_card_search_id: any = userdata.find((val) => {
    //         return val.userId === useridx;
    //     });
    //     return items_card_search_id?.userId;
    // }

    return (
        <>
        
            
                {console.log(userIndex(data.userId)+" hello")}
                <div className="eachItem" key={data.userId}>
                    <h3 >{data.userName}</h3>
                    <h3 >{data.userEmail}</h3>
                    <h3 >{data.userAge}</h3>
                    <div><i className="fas fa-edit" onClick={() => { editItems(data.userId); }}></i>
                        <i className="fas fa-trash-alt" onClick={() => { deleteItem(data.userId); }}></i>
                    <Link to={{ pathname: '/information', state: {userId: data.userId}}}>
                        <i className="fas fa-arrow-alt-circle-right" ></i> </Link ></div >

                </div >
           
            
        </>
    )
}

export default Displayuser;