
import React, { FC } from 'react'
import { useLocation } from 'react-router-dom';
import { useStoreState } from '../Redux/hooks';
import './style.css'

const TransferData = () => {
    const userdata = useStoreState((state) => {
        return state.users.items
    });
    // const UserIndex = () => {
    //     const items_card_search_id: any = userdata.find((val) => {
    //         return val.userId == 
    //     });
    //     console.log(items_card_search_id,"userIndex");
    //     return items_card_search_id;

    // }
    const location = useLocation()
    const userfethedid: any = location.state
    return (
        <>
            <div className="showItems" >
                <div className='eachItem'>

                    {userdata.map((data: any, key: number) => {
                       return (
                       <> { userfethedid.userId === data.userId ? <>
                       <h1 key={key}>Name: {data.userName}</h1> 
                       <h1 key={key}>Email: {data.userEmail}</h1> 
                       <h1 key={key+1}>Age: {data.userAge}</h1> </>
                       :  null
                        }
                    </>)})}

                </div>
            </div>
        </>
    )
}

export default TransferData
